import React from 'react'
import styled from 'styled-components'
import { Heading, Text, BaseLayout, Flex, Image } from '@pancakeswap/uikit'

import Page from 'components/Layout/Page'
import FarmStakingCard from 'views/Home/components/FarmStakingCard'
// import LotteryCard from 'views/Home/components/LotteryCard'
import CakeStats from 'views/Home/components/CakeStats'
import TotalValueLockedCard from 'views/Home/components/TotalValueLockedCard'
import EarnAPRCard from 'views/Home/components/EarnAPRCard'
import TwitterCard from 'views/Home/components/TwitterCard'
import WinCard from 'views/Home/components/WinCard'
import { useTranslation } from 'contexts/Localization'
import StakingPairCard from './components/StakingPairCard'
import EarnuptoPool from './components/EarnuptoPool'

const Hero = styled(Flex)`
  align-items: center;
  background-repeat: no-repeat;
  background-position: top center;
  display: flex;
  justify-content: space-evenly;
  
  margin: auto;
  margin-bottom: 32px;
  padding-top: 116px;
  text-align: center;

  ${({ theme }) => theme.mediaQueries.lg} {
    background-position: left center, right center;
    height: 165px;
    padding-top: 0;
  }
`

const Cards = styled(BaseLayout)`
  align-items: stretch;
  justify-content: stretch;
  margin-bottom: 32px;

  & > div {
    grid-column: span 6;
    width: 100%;
  }

  ${({ theme }) => theme.mediaQueries.sm} {
    & > div {
      grid-column: span 8;
    }
  }

  ${({ theme }) => theme.mediaQueries.lg} {
    & > div {
      grid-column: span 6;
    }
  }
`

const CTACards = styled(BaseLayout)`
  align-items: start;
  margin-bottom: 32px;

  & > div {
    grid-column: span 4;
  }

  ${({ theme }) => theme.mediaQueries.sm} {
    & > div {
      grid-column: span 6;
    }
  }

  ${({ theme }) => theme.mediaQueries.lg} {
    & > div {
      grid-column: span 4;
    }
  }
`

const HomePage = styled(Page)`
  width: 100%;
  background-image: url(/images/bg5.svg);
  background-repeat: no-repeat;
  background-position: center center;
`
const leftImage = '/images/leftImage.png'
const rightImage = '/images/rightImage.png'

const Home: React.FC = () => {
  
  const { t } = useTranslation()
  return (
    <Page>
      <Hero>
       {/* <Image src={leftImage} alt='logo' width={250} height={181}  /> */}
        <div>
          <Heading as="h1" scale="xl" mb="24px" color="text">
            {t('SKYLIGHT SWAP')}
          </Heading>
          <Text>{t('The #1 AMM and yield farm on Binance Smart Chain.')}</Text>
        </div>
        {/* <Image src={rightImage} alt='logo' width={250} height={181}  /> */}
        
      </Hero>
      <div style={{"maxWidth":"1200px", "margin":"auto"}}

      >
        <Cards>        
         
            <FarmStakingCard />
            <CakeStats />        
          
        </Cards>
        <CTACards>
          <EarnAPRCard />
          <EarnuptoPool />
          <TotalValueLockedCard />
          {/* <WinCard />
          <StakingPairCard /> */}
        </CTACards>
      </div>
    </Page>
  )
}

export default Home
