import React from 'react'
import { Card, CardBody, Heading, Text } from '@pancakeswap/uikit'
import styled from 'styled-components'
import { getBalanceNumber, formatLocalisedCompactNumber } from 'utils/formatBalance'
import { useTotalSupply, useBurnedBalance } from 'hooks/useTokenBalance'
import { usePriceCakeBusd } from 'state/farms/hooks'
import { getCakeAddress } from 'utils/addressHelpers'
import { useTranslation } from 'contexts/Localization'
import CardValue from './CardValue'

const StyledCakeStats = styled(Card)`
  margin-left: auto;
  margin-right: auto;
  background-image: url(/images/bg2.svg);
    background-repeat: no-repeat;
    background-position: right bottom;
    min-height: 476px;
    background-size: cover;
`

const Row = styled.div`
  align-items: center;
  display: flex;
  font-size: 14px;
  justify-content: space-between;
  margin-bottom: 8px;
`

const CardImage = styled.img`
  margin-bottom: 16px;
`
const PguText = styled(Text)`
    margin-bottom:16px;
    `
const CakeStats = () => {
  const { t } = useTranslation()
  const totalSupply = useTotalSupply()
  const burnedBalance = getBalanceNumber(useBurnedBalance(getCakeAddress()))
  const cakeSupply = totalSupply ? getBalanceNumber(totalSupply) - burnedBalance : 0
  const chyPriceBusd = usePriceCakeBusd()
  const mcap = chyPriceBusd.times(cakeSupply)
  return (
    <Card>
      <CardBody>
        <Heading scale="xl" mb="24px" >
          {t('SKY Stats')}
        </Heading>
        <CardImage src="/images/new/skyToken.png" alt="PGU logo" width={64} height={64} />
         <CardValue  value={cakeSupply} />
        <PguText  >{t('Total Token Supply')}</PguText>
        <CardValue  decimals={0} value={burnedBalance}/>
        <PguText  >{t('Total SKY Burned')}</PguText>

        <CardValue  decimals={6} value={chyPriceBusd.toNumber()} />
        <PguText  >{t('SKY Price')}</PguText>
        {/* <Row>
          <Text  fontSize="14px">{t('Market Cap')}</Text>
          <CardValue fontSize="14px" decimals={6} value={mcap.toNumber()} />
        </Row> */}
      </CardBody>
    </Card>
  )
}

export default CakeStats
